.about-us__hero { 
	width: 100%;
	max-width: 98rem;
	margin: 0 auto;
	padding: 4rem 2rem 6.8rem;

	@include bp(tab-up) {
		padding: 14rem 3rem 16.8rem;
	}
}

.about-us__hero-title { 
	@extend %secondary-font-light;

	margin-bottom: 3.5rem;

	font-size: 4rem;
	line-height: (52 / 40);
	color: palette(black, base);

	@include bp(tab-up) {
		font-size: 5.2rem;
		line-height: (72 / 52);
		text-align: center;
	}
}

.about-us__hero-copy {
	font-size: 2rem;
	line-height: (30 / 20);

	@include bp(tab-up) {
		font-size: 2.4rem;
		line-height: (36 / 24);
		text-align: center;
	}
}