.about-us__mission {
	padding: 5.4rem 2rem 0;
	background-color: palette(sanMarino, base);
	overflow: hidden;

	@include bp(tab-up) {
		padding: 9.6rem 3rem 0;
		text-align: center;
	}
}

.about-us__mission-copy {
	@extend %secondary-font-light;

	width: 100%;
	max-width: 92rem;
	margin: 0 auto;
	padding-bottom: 7rem;

	font-size: 2.4rem;
	line-height: (30 / 24);
	color: palette(white, base);

	@include bp(tab-up) {
		font-size: 3.6rem;
		line-height: (52 / 36);
		text-align: center;
	}
}

.about-us__mission-link {
	position: relative;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    align-items: center;

    margin-bottom: 2rem;

	font-size: 1.5rem;
	line-height: (36 / 15);
	color: palette(white, base);

	@include bp(tab-up) {
		display: inline-flex;
    	margin-bottom: 2.8rem;
	}

	span {
	    order: 0;
	    flex: 0 1 auto;
	    align-self: auto;

	}

	i {
		margin-left: 1.8rem;
	    order: 0;
	    flex: 0 1 auto;
	    align-self: auto;

	    background-color: palette(white, base);
	    border-radius: 50%;

	    font-size: 1.8rem;
		color: palette(sanMarino, base);
	}
}