.about-us__nurture {
	padding: 5.2rem 0 4rem;
	background-color: palette(white, base);

	@include bp(tab-up) {
		padding: 8rem 0 5.2rem;
	}
}

.about-us__nurture__ident {
    padding: 0 0 5.8rem;

    @include bp(mobile-large) {
    	padding: 0 2rem 5.8rem;
    }

	@include bp(tab-up) {
		padding: 0 0 6.4rem;
	}
}

.about-us__nurture__logo {

    display: block;
    position: relative;
    overflow: hidden;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: stretch;
    align-items: flex-start;
}

.about-us__nurture__logo-img {

    order: 0;
    flex: 0 1 auto;
    align-self: auto;
    max-width: 6.9rem;

    img {
        display: block;
        width: 100%;
    }

    @include bp(tab-up) {
    	max-width: 8.5rem;
    }
}

.about-us__nurture__logo-text {
	@extend %secondary-font-normal;

    order: 0;
    flex: 0 1 auto;
    align-self: auto;

    display: inline-block;
    font-size: 1.2rem;
    line-height: (24 / 14);
    color: palette(mineShaft, dark);
    margin: 0;

    @include bp(mobile-large) {
    	font-size: 1.4rem;
	}

    &:before {
	    content: "";
	    display: inline-block;
	    height: 2.6rem;
	    width: 0.1rem;
    	background-color: palette(mineShaft, dark);
	    vertical-align: middle;
	    margin: -0.6rem 0.9rem 0 0.9rem;

	    @include bp(mobile-large) {
	    	margin: -0.6rem 1.8rem 0 1.2rem;
	    }
	}

	@include bp(tab-up) {
		font-size: 1.8rem;
		line-height: (28 / 18);
	}
}

.about-us__nurture-title {
	@extend %secondary-font-light;

	margin: 0 auto 2rem;
	padding: 0 2rem;
	width: 100%;
	max-width: 60rem;

    color: palette(mineShaft, dark);
    font-size: 2rem;
    line-height: (34 / 20);

    @include bp(tab-up) {
	    font-size: 2.4rem;
	    line-height: (36 / 24);
	    text-align: center;
	}
}

.about-us__nurture-copy {
	@extend %primary-font-light;
	margin: 0 auto 1.6rem;
	padding: 0 2rem;
	width: 100%;
	max-width: 60rem;

    color: palette(mineShaft, dark);
    font-size: 1.8rem;
    line-height: (28 / 18);

    @include bp(tab-up) {
    	text-align: center;
    }
}