@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";
@import "../../../components/miyagi-core/styles/typography";

.about-us__locales {
	padding: 2rem;
	overflow: hidden;
	background-color: palette(white, base);

	@include bp(mobile-landscape-up) {
		padding: 3rem;
	}
}

.about-us__locales-content {
	max-width: 132rem;
	margin: 0 auto;

	@include bp(mobile-landscape-up) {
		display: flex;
	    flex-direction: row;
	    flex-wrap: wrap;
	    justify-content: flex-start;
	    align-content: stretch;
	    align-items: stretch;
	}
}

.about-us__locale {
	position: relative;
	display: block;
	margin-bottom: 2rem;
	background-color: palette(wildSand, base);

	@include bp(mobile-landscape-up) {

	    order: 0;
	    flex: 0 1 calc(50% - 1rem);
	    align-self: auto;

		&:nth-child(even) {
			margin-left: 2rem;
		}
	}

	@include bp(dt-up) {
	    flex: 0 1 calc(33.333% - 1.333rem);

		&:nth-child(even) {
			margin-left: 0;
		}

		&:nth-child(3n-1) {
			margin-left: 2rem;
		}

		&:nth-child(3n) {
			margin-left: 2rem;
		}
	}

	@include bp(max-out) {
	    flex: 0 1 calc(25% - 1.5rem);;

		&:nth-child(3n-1) {
			margin-left: 0;
		}

		&:nth-child(3n) {
			margin-left: 0;
		}

		&:nth-child(4n-2) {
			margin-left: 2rem;
		}

		&:nth-child(4n-1) {
			margin-left: 2rem;
		}

		&:nth-child(4n) {
			margin-left: 2rem;
		}
	}
}

.about-us__locale-content {
	width: 100%;
	padding: 3rem;

	@include bp(mobile-large) {
		padding: 4rem 3rem;
	}

	@include bp(mobile-landscape-up) {
		padding: 3rem;
	}

	@include bp(tab-up) {
		padding: 4rem 3rem;
	}
}

.about-us__locale-img {
	display: block;
	margin-bottom: 2rem;
	max-height: 5rem;
	width: auto;

	@include bp(mobile-large) {
		margin-bottom: 3.8rem;
	}

	@include bp(mobile-landscape-up) {
		margin-bottom: 2rem;
	}

	@include bp(tab-up) {
		margin-bottom: 3.8rem;
	}
}

.about-us__locale-title {
	@extend %primary-font-med;

	margin-bottom: 1.2rem;

	font-size: 1.6rem;
	line-height: (36 / 16);
	color: palette(sanMarino, base);
}

.about-us__locale-description {

	font-size: 1.4rem;
	line-height: (20 / 14);
	color: palette(mineShaft, base);
}
