.about-us__case-studies {
	width: 100%;
	padding: 0 2rem;

	@include bp(mobile-landscape-up) {
	    display: flex;
	    flex-direction: row;
	    flex-wrap: wrap;
	    justify-content: flex-start;
	    align-content: flex-start;
	    align-items: stretch;
	}

	@include bp(tab-up) {
	    display: block;
	}
}

.about-us__case-study {
	position: relative;
	margin: 0 auto 2rem;
	max-width: 132rem;
	overflow: hidden;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;

	@include bp(mobile-landscape-up) {
		margin: 0 1rem 2rem 0;
	    order: 0;
	    flex: 0 0 calc(50% - 1rem);
	    align-self: auto;

	    &:nth-child(even) {
	    	margin: 0 0 2rem 1rem;
	    }
	}

	@include bp(tab-up) {
		margin: 0 auto 3rem;
    	flex-direction: row;
    }

	@include bp(tab-up) {
		width: 100%;
    	flex-direction: row;

		&:nth-child(even) {
			margin: 0 auto 3rem;

			.about-us__case-study-image {
				order: 2;
			}

			.about-us__case-study-copy {
				order: 1;
			}
		}
	}
}

.about-us__case-study-image {
	position: relative;
	overflow: hidden;

    order: 1;
    flex: 0 1 auto;
    align-self: stretch;

	@include bp(dt-up) {
    	flex: 1 1 auto;
	}

	img {
		display: block;
		width: 100%;
		height: auto;
		z-index: 0;
	}
}


.about-us__case-study-image-label {
	position: absolute;
	left: 2rem;
	bottom: 1.5rem;

	z-index: 1;
	color: palette(white, base);


    @include bp(dt-up) {
		left: 3.6rem;
		bottom: 3.2rem;
    }
}

.about-us__case-study-copy {
	position: relative;

    order: 1;
    flex: 1 1 auto;
    align-self: stretch;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: flex-start;
    align-items: flex-start;

	@include bp(tab-up) {
    	flex: 0 0 33.333%;
	    justify-content: center;
    }

    @include bp(dt-up) {
    	position: relative;
        max-width: 42rem;
    }
}

.about-us__case-study-quote {
	@extend %secondary-font-light;

    order: 1;
    flex: 1 1 auto;
    align-self: auto;

	padding: 3rem;

	font-size: 1.6rem;
	line-height: (24 / 16);

	text-align: center;

	@include bp(mobile-large) {
		font-size: 2rem;
		line-height: (30 / 20);
	}

	@include bp(tab-up) {
   		flex: 0 1 auto;
		font-size: 1.6rem;
		line-height: (24 / 16);
	}

	@include bp(dt-up) {
		padding: 0 3rem 3rem;
		font-size: 2.4rem;
		line-height: (36 / 24);
	}
}

.about-us__case-study-link {
	@extend %primary-font-med;

    order: 1;
    flex: 0 1 auto;
    align-self: auto;

	display: block;
    width: 100%;
    padding: 24px 0;

	font-size: 1.5rem;
	line-height: (24 / 15);
	color: palette(sanMarino, base);
	text-align: center;
	text-transform: uppercase;

	@include bp(tab-up) {
		position: absolute;
		left: 0;
		bottom: 0;
	}
}